const uni = require('uni').default;















































    import {
        mapState,
        mapMutations
    } from 'vuex'

    export default {
			data(){
				return{
					img:'/static/img/nologin.png',
					msg:'未登录'
				}
				
			},
        computed: {
            ...mapState(['hasLogin', 'forcedLogin','userImg','userName']) 
        },
        methods: {
            ...mapMutations(['logout']),
            bindLogin() {
                uni.navigateTo({
                    url: '../login/login',
                });
            },
            bindLogout() {
                this.logout();
								console.log('------logout-------')
                /**
                 * 如果需要强制登录跳转回登录页面
                 */
                if (this.forcedLogin) {
                    uni.reLaunch({
                        url: '../login/login',
                    });
                }
            }
        }
    }



